'use strict';

var React = require('react');
var Reflux = require('reflux');
var If=require('../../../commons/if.jsx');


module.exports  = React.createClass({displayName: "exports",
  componentWillMount:function(){
    console.log('popup>componentWillMount');
    this.setState({tabId: 0});
  },

  componentDidUpdate: function(props,newState) { 
    console.log('popup>componentDidUpdate'); 
    this.props.onChange();
  },


  handleClick:function(tabId){
    console.log('popup>click');
    this.setState({'tabId':tabId});
    this.forceUpdate();
    
  },

  render: function() {
    console.log('popup>render id:' + this.props.id +" tab "+this.state.tabId);
    return (
      React.createElement("div", {className: "leaflet-popup-content-wrapper"}, 
        React.createElement("div", {className: "leaflet-popup-content"}, 
          React.createElement("div", {className: "panel panel-default"}, 
            React.createElement("div", {className: "panel-heading popup-header"}, 
              React.createElement("h3", {className: "panel-title"}, this.props.NAME_1, 
              React.createElement(If, {condition: this.props.NAME_2}, 
                "- ", this.props.NAME_2
              )
              )
            ), 
            React.createElement("div", {className: "panel-body"}, 
              React.createElement("span", {className: "fundingByType-title"}, React.createElement(Message, {message: "map.popup.funding"}), ": ", this.props.fundingUS?this.props.fundingUS.toFixed(2):0)
            )
          )
        )
      )
    );
}

});
