'use strict';

var React = require('react');
var Reflux = require('reflux');
var HighCharts = require('highcharts-browserify');
var InfoWindowActions=require('../../../../actions/infoWindowActions.js');
var InfoWindowStore=require('../../../../stores/infoWindowStore.js');
var If=require('../../../commons/if.jsx');
var Loading = require('../../../commons/loading.jsx')

var MyActivities = React.createClass({displayName: "MyActivities",
  render: function() {
    var items = [];
    if(this.props.data){
      items = this.props.data;
    }
    var link;
    if(this.props.externalFile){
      this.props.externalFile.map(function(l){link=l.value})
    }
    return (
      React.createElement("div", null, 
        React.createElement("div", {className: "subactivities-list"}, 
        React.createElement(Message, {message: "map.popup.programList"}), ": ", items.length, 
        React.createElement("ul", null, 
        
          items.map(function(node, index) {
            return React.createElement("li", null, node.name, " - (", node.value, ")")          
          })
        
        )), 
        React.createElement(If, {condition: link}, 
          React.createElement("div", null, React.createElement("a", {className: "btn download-button", href: link, target: "_blank"}, React.createElement(Message, {message: "map.popup.downloadFile"})))
        )
      )
    );
  }
});

module.exports  = React.createClass({displayName: "exports",
  mixins: [Reflux.connect(InfoWindowStore)],

  componentWillMount:function(){
    console.log('popup2>componentWillMount');
    this._getInfoWindowData(this.props.id, this.props.level, this.props.filters); 
  },

  _getInfoWindowData: function (id, level, filters) {
    var param = level? level.substring(0,2) : "de";
    var infoWindow = [{"param":param,"values":[id]}];
    var data = InfoWindowActions.getInfoFromAPI(infoWindow, filters) || [];
    return data;
  },

  componentDidMount: function() {
    console.log('popup2>componentDidMount');
    this._renderChart();
  },

  componentWillUpdate: function(props,newState) { 
    console.log('popup2>componentWillUpdate'); 
    var previousId = 0;
    if(newState.infoWindowFilter){
      newState.infoWindowFilter.map(function(node){node.values.map(function(innerNode){previousId = innerNode})});
    }
    if(previousId!=props.id || props.filters!=this.props.filters){
      this._getInfoWindowData(props.id, props.level, props.filters); 
    }
  },

  componentDidUpdate: function(props,newState) { 
    console.log('popup2>componentDidUpdate'); 
    this.props.onChange();
    this._renderChart();
  },

  setAttributeDisplay: function(classId, attr, display){
    $(classId).map(function(node, index) {
        if(index.getAttribute(attr)){
          index.style.display=display;
        }
      })
  },

  handleClick:function(tabId){
    console.log('popup2>click');
    this.setState({'tabId':tabId});
    this.forceUpdate();
    
  },

  _getTitles: function() {
    var titleArray = [];
    if(this.state.infoWindow){
      this.state.infoWindow.map(function(node, index) {
        titleArray.push(node.title);
      });
    }
    return titleArray;
  },

  _getData: function(tabId) {
    var infoData = [];
    if(this.state.infoWindow){
      this.state.infoWindow.map(function(node, index) {
        infoData.push(node.value);
      });
    }
    return infoData;
  },

  _renderChart: function() {
    console.log('popup2>_renderChart');
    var titleArray = this._getTitles();
    var infoData = this._getData();
    var tabId = this.state.tabId ? this.state.tabId : 0;

    if(infoData.length>0 && infoData.length>tabId && infoData[tabId].length>0){
      if(tabId!=4 ){
        var chartdata = [];
        var totalValue = 0;
        infoData[tabId].map(function(node, index) {
            totalValue += parseInt(node.value);
        });
        infoData[tabId].map(function(node, index) {
          var chartnode = [];
          chartnode.push(node.name);
          chartnode.push(parseInt(parseInt(node.value)/totalValue*100));
          chartdata.push(chartnode);
        });

        var chart = new HighCharts.Chart({
            colors: ['#FFC614', '#3897D3', '#18577A', '#97CB68', '#ED561B', '#DDDF00', '#24CBE5', '#64E572', '#50B432', '#FF9655', '#FFF263', '#6AF9C4'],
            chart: {
              marginTop: 20,
              width: 420,
              height: 240,
              plotBorderWidth: null,
              renderTo: 'container',
              type: 'pie',
            },
            title: {
              align: "left",
              text: titleArray[tabId],
              style: { 
                "color": "#4278AA", 
                "fontSize": "14px" 
              }
            },
            plotOptions: {
              pie: {
                  innerSize: "70%",
                  name: 'Cantidad total',
                  animation: false,
                  dataLabels: {
                      enabled: false
                  },
                  showInLegend: true
              }
            },
            legend: {
              enabled: true,
              layout: 'vertical',
              align: 'right',
              itemStyle: {
                  color: '#4F4F4F'
              },
              verticalAlign: 'middle',
              labelFormatter: function() {
                var name = this.name.length>21?this.name.substring(0,20):this.name;
                return name + ' ' + this.y + '%';
              }
            },
            series: [{data: chartdata}]
          });
      }

    }
    this.setAttributeDisplay(".popup-nav-wrapper", "data-originalreactid", "inline");
  },

  render: function() {
    console.log('popup2>render id:' + this.props.id +" tab "+this.state.tabId);
    var tabId = this.state.tabId ? this.state.tabId : 0;
    var titleArray = this._getTitles();
    var infoData=[];
    if(tabId==4){
      infoData = this._getData(tabId);
    }
    var showLoading=true;
    if(this.state.infoWindow){
      showLoading=false;
    }
    return (
      React.createElement("div", {className: "leaflet-popup-content-wrapper"}, 
        React.createElement("div", {className: "leaflet-popup-content"}, 
          React.createElement("div", {className: "panel panel-default"}, 
            React.createElement("div", {className: "panel-heading popup-header"}, 
              React.createElement("h3", {className: "panel-title"}, this.props.name), 
              React.createElement("span", {className: "title-label"}, " - ", React.createElement(Message, {message: "map.popup.totalActivities"}))
            ), 
            React.createElement("div", {className: "popup-nav-wrapper"}, 
              React.createElement("nav", {className: "tabs", role: "tablist"}, 
                React.createElement("ul", {className: "tabs nav nav-tabs", role: "tablist"}, 
                React.createElement("li", {className: "active", role: "tab"}, 
                  React.createElement("a", {href: "#", onClick: this.handleClick.bind(this, 0)}, 
                    React.createElement("span", {className: "popup-icon chart", title: "Cost Share Breakdown"})
                  )
                ), 
                React.createElement("li", {className: "", role: "tab"}, 
                  React.createElement("a", {href: "#", onClick: this.handleClick.bind(this, 1)}, 
                    React.createElement("span", {className: "popup-icon funding-dev-obj", title: "Development Objectives"})
                  )
                ), 
                React.createElement("li", {className: "", role: "tab"}, 
                  React.createElement("a", {href: "#", onClick: this.handleClick.bind(this, 2)}, 
                    React.createElement("span", {className: "popup-icon subactivities", title: "Activity Classication"})
                  )
                ), 
                React.createElement("li", {className: "", role: "tab"}, 
                  React.createElement("a", {href: "#", onClick: this.handleClick.bind(this, 3)}, 
                    React.createElement("span", {className: "popup-icon export", title: "Public Private Partnership"})
                  )
                ), 
                React.createElement("li", {className: "", role: "tab"}, 
                  React.createElement("a", {href: "#", onClick: this.handleClick.bind(this, 4)}, 
                    React.createElement("span", {className: "popup-icon subactivitiesList", title: "Sub Activities"})
                  )
                )
                )
              )
            ), 
            React.createElement("div", {className: "panel-body"}, 
              React.createElement("div", {className: "chart-container", id: "container"}), 
              React.createElement(If, {condition: showLoading}, 
                React.createElement(Loading, {container: "popup-loading-container"})
              ), 
              React.createElement(If, {condition: tabId==4 && !showLoading}, 
                React.createElement("div", {className: "subactivities-container"}, 
                  React.createElement("div", {className: "sub-activities-title"}, titleArray[tabId]), 
                  React.createElement(MyActivities, {data: infoData[4], externalFile: infoData[5]})
                )
              )
            )
          )
        )
      )
    );
}

});
